import React, { useContext } from 'react';


function Square(props) {
    return (
        <button
            className="square"
            onClick={() => { props.onClick({value: 'X'}) }}
        >
            {props.value}
        </button>
    )
}

class Board extends React.Component {
    renderSquare(i) {
        return <Square
            value={this.props.squares[i]}
            onClick={() => this.props.onClick(i)}
        />
    }
    render() {
        return (
            <div>
                <div className="board-row">
                    {this.renderSquare(0)}
                    {this.renderSquare(1)}
                    {this.renderSquare(2)}
                </div>
                <div className="board-row">
                    {this.renderSquare(3)}
                    {this.renderSquare(4)}
                    {this.renderSquare(5)}
                </div>
                <div className="board-row">
                    {this.renderSquare(6)}
                    {this.renderSquare(7)}
                    {this.renderSquare(8)}
                </div>
            </div>
        )
    }
}

export default class Game extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            history: [{
                squares: Array(9).fill(null)
            }],
            xIsNext: true,
        }
    }
    handleClick(i) {
        const history = this.state.history
        const current = history[history.length - 1]
        const squares = current.squares.slice()
        if (calculateWinner(squares) || squares[i]) {
            return;
        }
        squares[i] = this.state.xIsNext ? 'X' : 'O'
        this.setState({
            history: history.concat([{
                squares: squares
            }]),
            xIsNext: !this.state.xIsNext,
        })
    }
    render() {
        const history = this.state.history
        const current = history[history.length -1]
        const winner = calculateWinner(current.squares)
        let status
        if (winner) {
            status = 'Winner: ' + winner
        } else {
            status = 'Next player: ' + (this.state.xIsNext ? 'X' : 'O')
        }
        return (
            <div className="game">
                <div className="game-board">
                    <Board
                    squares={current.squares}
                    onClick={(i) => this.handleClick(i)}
                    />
                </div>
                <div className="game-info">
                    <div>{status}</div>
                    <ol>{/* TODO */}</ol>
                </div>
            </div>
        )
    }
}

function calculateWinner(squares) {
    if (squares) {
        for (let i = 0; i < squares.length; i++) {
            const matchingRow = squares[i] && squares[i] === squares[i+1] && squares[i] === squares[i+2]
            const matchingColumn = squares[i] && squares[i] === squares[i+3] && squares[i] === squares[i+6]
            const matchingDiagonal = squares[i] && squares[i] === squares[i+4] && squares[i] === squares[i+8]
            if (matchingRow || matchingColumn || matchingDiagonal) {
                return squares[i]
            }
        }
    }
    return null
}